.end {
	text-align: center;
	padding-top: 86px;
	padding-bottom: 79px;
	background: url('../img/end.jpg') center no-repeat;
	background-size: cover;
	position: relative;
	z-index: 2;
	color: #fff;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		height: 100%;
		background: rgba(#111, 0.4);
		z-index: -1; }
	&_title {
		@extend .features_title;
		color: #fff; }
	img {
		margin: auto; }
	&_items {
		margin-top: 45px;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		flex-wrap: wrap; }
	&_item {
		flex-basis: 18%; }
	&_img {
		height: 114px;
		width: 114px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: auto;
		border-radius: 50%;
		background-color: #b4e2ff; }
	&_about {
		margin-top: 25px;
		color: #efefef;
		font-size: 17px;
		font-weight: 300;
		line-height: 1.4;
		padding: 0 10px; } }

@media (max-width: 1130px) {
	.end {
		&_items {
			justify-content: space-between; }
		&_item {
			flex-basis: 44%;
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0; } } } }

@media (max-width: 576px) {
	.end {
		padding: 50px 0;
		&_item {
			flex-basis: 100%; } } }
