.vebinar {
	padding-top: 80px;
	padding-bottom: 60px;
	text-align: center;
	img {
		margin: auto;
		margin-bottom: 35px; }
	&_title {
		color: #252525;
		font-size: 28px;
		font-weight: 400;
		text-transform: uppercase;
		line-height: 1;
		span {
			display: block;
			color: #299cbd;
			font-size: 34px;
			font-weight: 900;
			margin-top: 10px;
			margin-bottom: 25px; } }
	&_small {
		color: #4a4a4a;
		font-size: 23px;
		font-weight: 400;
		margin: 0; } }

@media (max-width: 576px) {
	.vebinar {
		&_title {
			font-size: 24px;
			line-height: 1.4;
			span {
				font-size: 30px;
				margin-bottom: 15px; } }
		&_small {
			font-size: 14px; } } }
