.footer {
	padding-top: 60px;
	padding-bottom: 70px;
	background-color: #1a1a1a;
	color: #777;
	text-align: center;
	a {
		color: #777; }
	&_links {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		margin-bottom: 15px;
		li {
			&::after {
				content: '|';
				margin-left: 5px;
				margin-right: 5px; }
			&:last-child::after {
				display: none; }
			a {
				&:hover {
					color: #af8; } } } }
	span {
		a {
			text-decoration: none;
			color: #fea100;
			&:hover {
				text-decoration: underline; } } } }


