.begin {
	background: url('../img/main.jpg') center no-repeat;
	background-size: cover;
	position: relative;
	z-index: 2;
	min-height: 100vh;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background: rgba(#111, 0.8);
		width: 100%;
		height: 100%;
		z-index: -1; }
	&_top {
		padding-top: 40px;
		padding-bottom: 50px;
		&_logo {
			margin-bottom: 15px;
			text-align: center; }
		&_name {
			color: #ffffff;
			font-size: 15px;
			line-height: 24px;
			text-transform: uppercase;
			text-align: center;
			letter-spacing: 0.4px; } }
	&_main {
		&_title {
			color: #f9bf3b;
			font-size: 41px;
			text-align: center;
			font-weight: 900;
			text-transform: uppercase;
			margin: 0;
			line-height: 1;
			span {
				color: #ffffff;
				font-size: 80px;
				font-weight: 900;
				display: block;
				margin: 20px 0; } }
		&_text {
			margin: 0;
			margin-top: 25px;
			color: #fff;
			font-weight: 300;
			text-align: center;
			font-size: 21px;
			line-height: 1.5;
			span {
				color: #f9bf3b;
				font-weight: 900; } }
		&_button {
			margin: 70px auto;
			padding: 29px;
			box-shadow: inset 0 -5px 0 rgba(0, 0, 0, 0.18);
			border: 4px solid rgba(0, 0, 0, 0.23);
			background-image: linear-gradient(to top, #2798b9 0%, #3ab6da 100%);
			cursor: pointer;
			text-shadow: -1px -1px 0 rgba(0, 0, 0, 0.22);
			color: #ffffff;
			font-size: 18px;
			text-transform: uppercase;
			display: block;
			transition: color 0.4s;
			letter-spacing: 1px;
			&:hover {
				color: #f9bf3b; } } }
	&_bottom {
		text-align: center;
		padding-bottom: 70px;
		&_text {
			color: #ffffff;
			font-size: 15px;
			text-transform: uppercase;
			font-weight: 300;
			margin: 0;
			margin-bottom: 15px; }
		&_arrow {
			animation-name: arrows;
			animation-duration: 5s;
			animation-iteration-count: infinite;
			animation-timing-function: linear; } } }

@keyframes arrows {
	0% {
		transform: translateY(0);
		opacity: 1; }
	20% {
		transform: translateY(10px);
		opacity: 0.9; }
	40% {
		transform: translateY(20px);
		opacity: 0.8; }
	60% {
		transform: translateY(30px);
		opacity: 0.7; }
	80% {
		transform: translateY(40px);
		opacity: 0.6; }
	100% {
		transform: translateY(50px);
		opacity: 0.5; } }


@media (max-width: 1130px) {
	.begin {
		&_top {
			padding-top: 20px;
			padding-bottom: 30px; }
		&_main {
			&_title {
				font-size: 32px;
				span {
					font-size: 48px; } }
			&_button {
				margin: 25px auto; }
			&_text {
				font-size: 18px; } } } }

@media (max-width: 576px) {
	.begin {
		&_main {
			&_button {
				padding: 20px 10px;
				font-size: 16px; }
			&_title {
				font-size: 24px;
				line-height: 1.25;
				span {
					font-size: 36px; } } } } }
