.optimization {
	padding-top: 102px;
	padding-bottom: 107px;
	&_title {
		color: #299cbd;
		margin-left: auto;
		font-size: 30px;
		font-weight: 900;
		text-transform: uppercase;
		letter-spacing: 1px;
		margin-bottom: 25px; }
	&_text {
		margin-bottom: 15px;
		letter-spacing: 0.4px;
		color: #4a4a4a;
		font-size: 17px;
		font-weight: 300;
		line-height: 1.6;
		&:last-child {
			margin-bottom: 0; } }
	.bold {
		font-weight: 900; }
	&_wrapper {
		max-width: 770px;
		margin-left: auto; }
	img {
		position: absolute;
		top: -20px;
		left: -220px;
		z-index: -1; } }

@media (max-width: 1130px) {
	.optimization {
		img {
			display: none; }
		&_wrapper {
			margin: auto; }
		&_title {
			font-size: 24px; }
		&_text {
			font-size: 16px; } } }

@media (max-width: 576px) {
	.optimization {
		padding: 50px 0;
		text-align: center; } }
