*, *::after, *::before {
	box-sizing: border-box; }

body {
	margin: 0;
	font-family: 'Roboto', sans-serif; }

img {
	display: block;
	max-width: 100%;
	height: auto; }

button, input, textarea {
	outline: none; }

a {
	display: inline-block; }

ul {
	margin: 0;
	padding: 0;
	list-style-type: none; }

h1,h2,h3,h4,h5,h6 {
	margin: 0;
	display: block; }

.container {
	max-width: 1170px;
	padding: 0 15px;
	margin: 0 auto;
	position: relative; }



