.present {
	background-color: #efefef;
	padding: 85px 0;
	text-align: center;
	img {
		margin: auto;
		margin-bottom: 40px; }
	&_text {
		color: #252525;
		font-size: 30px;
		font-weight: 400;
		line-height: 1.5;
		margin: 0;
		margin-bottom: 50px; }
	&_btn {
		@extend .begin_main_button;
		margin-bottom: 0; } }

@media (max-width: 1130px) {
	.present {
		&_text {
			font-size: 18px;
			max-width: 540px;
			margin: auto; }
		&_btn {
			margin: 40px auto;
			margin-bottom: 0; } } }

@media (max-width: 576px) {
	.present {
		padding: 50px 0; } }
