.features {
	padding-top: 85px;
	padding-bottom: 75px;
	background-color: #efefef;
	text-align: center;
	&_title {
		font-size: 28px;
		color: #252525;
		text-transform: uppercase;
		font-weight: 400;
		&::after {
			content: '';
			display: block;
			margin: 18px auto;
			width: 216px;
			background-color: #f9bf3f;
			height: 4px; } }
	&_subtitle {
		display: block;
		color: #4a4a4a;
		font-size: 23px;
		margin-bottom: 45px; }
	&_items {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&_item {
		flex-basis: 30%; }
	&_img {
		margin: auto; }
	&_info {
		color: #4a4a4a;
		font-size: 15px;
		font-weight: 300;
		line-height: 1.4;
		margin: 0;
		margin-top: 30px; } }

@media (max-width: 1130px) {
	.features {
		&_items {
			flex-direction: column;
			align-items: center;
			justify-content: center; }
		&_item {
			margin-bottom: 50px;
			&:last-child {
				margin-bottom: 0; } }
		&_info {
			max-width: 540px;
			font-size: 16px;
			line-height: 1.7;
			margin-top: 20px; } } }

@media (max-width: 576px) {
	.features {
		padding: 50px 0;
		&_title {
			font-size: 24px; }
		&_subtitle {
			font-size: 20px; } } }

